.rmc-tabs {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  height: 100%;
  width: 100%;
}
.rmc-tabs-content-wrap {
  display: flex;
  height: 100%;
  width: 100%;
}
.rmc-tabs-content-wrap-animated {
  transition: transform 0.3s cubic-bezier(0.35, 0, 0.25, 1);
  will-change: transform;
}
.rmc-tabs-pane-wrap {
  width: 100%;
  flex-shrink: 0;
  overflow-y: auto;
}
.rmc-tabs-pane-wrap-active {
  height: 100%;
}
.rmc-tabs-pane-wrap-inactive {
  height: 0;
  overflow: visible;
}
.rmc-tabs-top .rmc-tabs-tab-bar-wrap {
  border-bottom: 1px #eee solid;
}
.rmc-tabs-bottom .rmc-tabs-tab-bar-wrap {
  border-top: 1px #eee solid;
}
.rmc-tabs-tab-bar {
  display: flex;
  flex-direction: column;
  width: 100%;
  background: #fff;
}
.rmc-tabs-tab-bar-tabs {
  display: flex;
}
.rmc-tabs-tab-bar-tab {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 0;
}
.rmc-tabs-tab-bar-underline {
  position: relative;
  border: 1px #108ee9 solid;
}
.rmc-tabs-tab-bar-animated .rmc-tabs-tab-bar-underline {
  transition: margin-left 0.3s cubic-bezier(0.35, 0, 0.25, 1);
}



